body {
    font-family: 'Cabin', sans-serif;
    margin: 0;
    box-sizing: border-box;
}

header,
footer {
    min-height: 100vh;
    text-transform: uppercase;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
}
header dd {
    margin: 1rem;
}
header nav {
    margin: 2rem;
}
header nav ul {
    list-style: none;
    display: inline-flex;
    padding: 0;
}
header nav ul li {
    margin: 0 1rem;
}
header nav ul li a {
    color: darkgrey;
    text-decoration: none;
}
header nav ul li a:hover {
    color: inherit;
}

section {
    min-height: 100vh;
    box-sizing: border-box;
}

#details {
    height: 100vh;
}

#map {
    height: 100%;
}

#synopsis {
    padding: 2rem;
    background-color: lightgray;
}

footer {
    background-color: grey;
    color: white;
}

@media (min-width: 860px) {
    #synopsis {
        padding: 4rem;
    }
}

#top {
    display: none;
    border-radius: 0.5rem;
    padding: 1rem;
    position: fixed;
    bottom: 2rem;
    right: 2rem;
    color: darkgray;
    text-decoration: none;
}
#top:hover {
    background-color: white;
    color: black;
}
